import React from 'react';
import { BrowserRouter, Link, Route } from 'react-router-dom/cjs/react-router-dom.min';
import logo from "../images/logo.png"

import audio from '../audio/bike_walk.wav'

function playAudio() {
    new Audio(audio).play();
}

class Nav extends React.Component {
    render() {
        return (
            <Link to="/website">
            <div class="nav" onClick={playAudio}>
                {/* <h1 className="title">Slacker Bikes</h1> */}
                <img src={logo}></img>
            </div>
            </Link>
        );
    }
}

export default Nav;